// app/components/MovieCarousel.tsx
'use client';
import Image from 'next/image';
import { useState } from 'react';
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/solid';

interface MovieCarouselProps {
  movies: any[];
  onMovieSelect: (movie: any) => void;
  selectedMovieId: number;
}

export default function MovieCarousel({ movies, onMovieSelect, selectedMovieId }: MovieCarouselProps) {
  const [startIndex, setStartIndex] = useState(0);
  const visibleMovies = 6; // Number of movies visible at once

  const slideLeft = () => {
    setStartIndex(prev => Math.max(0, prev - 1));
  };

  const slideRight = () => {
    setStartIndex(prev => Math.min(movies.length - visibleMovies, prev + 1));
  };

  return (
    <div className="relative">
      <div className="flex items-center">
        <button
          onClick={slideLeft}
          className="absolute left-0 z-10 bg-black/50 p-2 rounded-full text-white hover:bg-black/70 transform -translate-x-1/2"
          disabled={startIndex === 0}
        >
          <ChevronLeftIcon className="h-6 w-6" />
        </button>

        <div className="flex overflow-hidden gap-4 px-8">
          {movies.slice(startIndex, startIndex + visibleMovies).map((movie) => (
            <div 
              key={movie.id}
              className={`flex-none w-48 cursor-pointer transition-transform duration-300 hover:scale-105
                ${selectedMovieId === movie.id ? 'ring-2 ring-yellow-500 rounded-lg' : ''}`}
              onClick={() => onMovieSelect(movie)}
            >
              <div className="relative aspect-[2/3] rounded-lg overflow-hidden">
                <Image
                  src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                  alt={movie.title}
                  fill
                  className="object-cover"
                  sizes="(max-width: 768px) 30vw, 20vw"
                />
              </div>
            </div>
          ))}
        </div>

        <button
          onClick={slideRight}
          className="absolute right-0 z-10 bg-black/50 p-2 rounded-full text-white hover:bg-black/70 transform translate-x-1/2"
          disabled={startIndex >= movies.length - visibleMovies}
        >
          <ChevronRightIcon className="h-6 w-6" />
        </button>
      </div>
    </div>
  );
}